.recommend {
  height: calc(100vh - 50px - 56px );
  overflow: hidden;
  overflow-y: scroll;
  @extend %scroll-bar;
  padding: 0 20px;
  > div {
    width: 75%;
    min-width: 900px;
    max-width: 1050px;
    // padding: 0 30px;
    margin: 0 auto;
  }
  .recommend-panel-title {
    .fl {
      float: left;
    }
    .fr {
      float: right;
      font-size: .8rem;
      vertical-align: text-bottom;
      cursor: pointer;
      color: rgba(0,0,0,.8);
      &:hover {
        color: black;
      }
    }
    overflow: hidden;
    border-bottom: 1px solid #ccc;
    padding-bottom: 12px;
  }

  .playlist-item-container {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    flex-wrap: wrap;
  }
}